.container {
    max-width: 1240px;
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
}

.container.column {
    flex-direction: column;
    align-items: center;
}

.container.reverse {
    flex-direction: row-reverse;
}

@media (max-width: 900px) {
    .container {
        flex-wrap: wrap;
        padding-left: 8px;
        padding-right: 8px;
    }
}