:root {
  --color-primary: #062446;
  --color-secondary: #768493;
  --color-surface: #f0f5fa;
  --color-link-active: #007aff;
  --color-link-hover: #007affd9;
  --color-button-primary: #007aff;
  --color-button-hover: #0068d8;
  --background-button: #0924430a;
  --background-button-hover: #09244314;
  --color-selected: #e4ebfa;

  --border-radius-button: 4px;
  --border-radius-dialog: 7px;
  --border-radius-container: 4px;
  --container-padding: 16px;
  --button-margin: 8px;

  --button-shadow: 0 0 0 1px #00000012;

  --font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
