.tryfree-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    pointer-events: none;
}

.tryfree-modal {
    max-width: 100%;
    width: 500px;
    padding: 40px;
    border-radius: 6px;
    background: white;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.tryfree-modal-closebutton {
    position: absolute;
    top: 40px;
    right: 40px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    background: #f4f7f9;
    cursor: pointer;
}

.tryfree-modal-closebutton-img {
    display: block;
    width: 28px;
    height: 28px;
    margin: 5px;
}

.tryfree-modal-content {
    max-width: 440px;
    padding: 40px 0 0;
    margin: 0 auto;
    text-align: center;
}

.tryfree-modal-content-title {
    line-height: 38px;
    font-size: 26px;
    font-weight: 400;
}

.tryfree-modal-content-form {
    margin: 60px auto 0;
}

.tryfree-modal-content-form-input {
    display: block;
    width: 100%;
    height: 50px;
    margin: 0 auto 18px;
    line-height: 23px;
    padding: 14px 20px 13px;
    border: 1px solid #c5d2d9;
    outline: 0;
    box-shadow: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 400;
}

.tryfree-modal-content-form-button {
    color: white;
    background: var(--color-link-active);
    min-width: 164px;
    margin: 20px auto 0;
    display: inline-block;
    height: 50px;
    padding: 0 24px;
    border: 0;
    outline: 0;
    box-shadow: none;
    border-radius: 50px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.tryfree-modal-content-comment {
    line-height: 21px;
    margin: 40px auto 0;
    font-size: 14px;
    font-weight: 400;
}

@media (max-width: 900px) {
    .tryfree-modal{
        position: fixed;
        padding: 10px;
        width: 90%;
    }
    .tryfree-modal-content-comment{
        margin: 5px 0 0;
    }
    .tryfree-modal-content-form{
        margin: 0;
    }
    .tryfree-modal-content-form-button{
        margin: 0;
    }
    .tryfree-modal-closebutton{
        top: 10px;
        right: 10px;
    }
} 

.agreement {
    text-align: center;
    font-size: 14px;
    color: var(--color-primary);
    opacity: .5;
}