.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
}

.navContent{
    display: flex;
    justify-content: space-between;
}

header{
    position: sticky;
    top: 0px;
}

.navBar{
    height: 70px;
    background-color: #FFFFFF;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    align-items: center;
    display: flex;
}

.logo{
    
}

.buttons{
    display: flex;
    gap: 40px;
}

.link{
    display: flex;
    color: #667085;
    font-size: 16px;
    font-weight: 500;
    align-items:center;
    justify-content:center;
    border-bottom: 2px solid rgba(0,0,0,0);
    text-decoration: none;
}

.link:hover{
    border-bottom: 2px solid #2970FF;
}


.banner{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 175px;
    line-height: 25px;
}

h1{
    color: #2970FF;
    font-size: 34px;
    font-weight: 600;
    margin: 0;
}

.paragraph{
    color: #667085;
    font-weight: 300;
    font-size: 17px;
}

.demoButton{
    margin-top: 50px;
    height: 50px;
}

.morda{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}

.capability{
    margin-top: 91px;
    display: grid;
    grid-template-columns: 50% 50%;
    line-height: 31px;
}

.capability figure{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.capability h3{
    color: #2970FF;
    font-size: 24px;
    font-weight: 600;
}

.capability span{
    color: #667085;
    font-size: 20px;
    font-weight: 300;
}

.b3{
    display: flex !important;
    flex-direction: row !important;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    gap: 20px;
}

.b3 .Image{
    display: flex;
    height: 100%;
    padding-top: 2px;
}

.b3 h3{
    margin: 0;
}

.b3Content{
    padding: 30px 120px;
}

.b4{
    display: flex;
    padding-top: 27px;
    padding-bottom: 27px;
    min-height: 640px;
}

.b4 .content{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-top: 173px;
    padding-left: 42px;
    padding-right: 42px;
}

.ButtonSquare{
    border-radius: 0 !important;
    height: 50px !important;
}

.inputPanel{
    display: grid;
    grid-template-columns: 35% 35% 30%;
    width: 100%;
}