.action-button {
    display: flex;
    font-size: 14px;
    background-color: #2970FF;
    border-radius: 15px;
    color: #FFFFFF;
    font-weight: 600;
    padding: 0 30px;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    height: 40px;
    border: none;
    cursor: pointer;
}

.action-button:hover,
.action-button:focus,
.action-button--active {
    background: #155EEF;
    text-decoration: none;
}