a{
    text-decoration: none;
}
.footer {
    background: #262C3A;
    padding-top: 100px;
    padding-bottom: 100px;
}

.footer-column {
    display: flex;
    flex-direction: column;
    flex-basis: 200px;
}

.footer-column.main-column {
    flex-basis: 450px;
}

.phone-title {
    font-size: 13px;
    color: var(--color-link-active);
    margin-bottom: 2px;
    margin-top: 0;
}

.footer-phone {
    font-size: 34px;
    color: white;
    padding-bottom: 24px;
}

.footer-phone:hover {
    color: #51a0d9;
}

.footer-column-title {
    color: var(--color-link-active);
    font-size: 16px;
    font-weight: 600;
    margin-top: 0;
}

.footer-column-link {
    padding-bottom: 8px;
    color: #fff;
}

.footer-column-link:hover {
    color: #51a0d9;
}

.footer .logo {
    padding-bottom: 30px;
}

.footer .logo-img {
    height: 56px;
}

@media (max-width: 900px) {
    .footer {
        padding-top: 10px;
        padding-bottom: 0px;
    }
    .footer-column.main-column {
        flex-basis: unset;
    }
    .footer-logo {
        display: flex;
        justify-content: center;
    }
    .footer .container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }
}