.banner-btn {
    display: flex;
    font-size: 14px;
    background-color: #2970FF;
    border-radius: 15px;
    color: #FFFFFF;
    font-weight: 600;
    padding: 0 30px;
    text-decoration: none;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    height: 40px;
    cursor: pointer;
}

.banner-btn:hover,
.banner-btn:focus,
.banner-btn--active {
    background: #155EEF;
    text-decoration: none;
}