.logo {
    display: flex;
    margin-top: 10px;
}

.logo:focus, .logo:hover {
    color: #fff;
    text-decoration: none;
}

.logo-text {
    text-transform: uppercase;
    font-size: 18px;
    padding-top: 2px;
    margin-left: 2px;
}

.logo-img {
    width: auto;
    height: 40px;
}

.logo-img.hide {
    display: none;
}

@media (max-width: 900px) {
    .logo {
        padding-top: 10px;
    }
    .logo-img{
        height: 35px;
    }
    #inverted-logo{
        display: none;
    }
    .logo-img.hide{
        display: block;
    }
}